import { RegistrationService } from './../../../services/registration.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: [
    './register.component.scss'
  ]
})
export class RegisterComponent implements OnInit {
  hidden = false;

  interests: any[] = [
    { interest: 'Java', isSelected: true }, { interest: 'Python' },
    { interest: 'C++' }, { interest: 'Ruby' },
    { interest: 'Angular' }, { interest: 'Basic Web' }, { interest: 'i dont know yet' }
  ];

  isPrivacyAccepted = false;
  experiences: any[] = [
    { experence: 'No experience', description: 'I have never coded before, neither do i have any prfessional experience' },
    { experence: 'Beginner', description: 'I know how to use loops, conditional statements, variables, methods' },
    {
      experence: 'Intermediate',
      description: 'I have a good understanding of object oriented programming and I can develop small applications'
    },
    { experence: 'Advanced', description: 'I was involved in development process for multiple projects' },
  ];
  model: any = {};
  constructor(private traineeRegistrationService: RegistrationService, private router:Router) { }

  ngOnInit(): void {

  }
  redirect() {
    window.location.href = 'https://paystack.com/pay/blingcodes';
    // this.router.navigate(['masterclass/callback']);
  }
  submit() {
    this.traineeRegistrationService.registerTrainee(this.model).subscribe(
      (response)=>{
        this.redirect();
      },
      (error)=>{
        console.log(error);
      }
    )
  }

}
