import { PaymentCallbackComponent } from './views/payment-callback/payment-callback.component';
import { TermsComponent } from './views/terms/terms.component';
import { PrivacyComponent } from './views/privacy-policy/privacy-policy.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthComponent } from './layouts/auth/auth.component';

import { RegisterComponent } from './views/auth/register/register.component';

// no layouts views
import { IndexComponent } from './views/index/index.component';
import { LandingComponent } from './views/landing/landing.component';
import { ProfileComponent } from './views/profile/profile.component';

const routes: Routes = [

  // auth views
  {
    path: 'masterclass',
    component: AuthComponent,
    children: [
      { path: 'register', component: RegisterComponent },
      { path: '', redirectTo: 'login', pathMatch: 'full' },
      { path: 'callback', component: PaymentCallbackComponent }
    ],
  },
  // no layout views
  { path: 'profile/:user', component: ProfileComponent },
  { path: 'support', component: LandingComponent },
  { path: 'privacy-policy', component: PrivacyComponent },
  { path: 'terms', component: TermsComponent },

  { path: '', component: IndexComponent },
  { path: '**', redirectTo: '', pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule { }
